import React, { useState } from 'react';
import { Input, Radio, RadioGroup, Stack, Button, useToast } from '@chakra-ui/react';
import axios from 'axios';
import { useRouter } from 'next/router';

const MainRegister = () => {
  const router = useRouter();
  const toast = useToast();

  const [step, setStep] = useState(0);
  const [formData, setFormData] = useState({
    username: '',
    email: '',
    password: '',
    first_name: '',
    last_name: '',
    birth_date: '',
    gender: '',
    location: '',
    bio: '',
    image_url: null,
  });

  const updateFormData = (key, value) => {
    setFormData((prev) => ({ ...prev, [key]: value }));
  };

  const steps = [
    {
      title: 'Choose a Username',
      description: 'This will be your unique identifier.',
      key: 'username',
      component: (
        <Input
          placeholder='Enter your username'
          value={formData.username}
          onChange={(e) => updateFormData('username', e.target.value)}
        />
      ),
    },
    {
      title: 'What\'s your email?',
      description: 'To create or login to your account',
      key: 'email',
      component: (
        <Input
          placeholder='Enter your email'
          type='email'
          value={formData.email}
          onChange={(e) => updateFormData('email', e.target.value)}
        />
      ),
    },
    {
      title: 'Choose a Password',
      description: 'Make sure it\'s strong and secure.',
      key: 'password',
      component: (
        <Input
          placeholder='Enter your password'
          type='password'
          value={formData.password}
          onChange={(e) => updateFormData('password', e.target.value)}
        />
      ),
    },
    {
      title: 'Enter your first name',
      key: 'first_name',
      component: (
        <Input
          placeholder='Enter your first name'
          value={formData.first_name}
          onChange={(e) => updateFormData('first_name', e.target.value)}
        />
      ),
    },
    {
      title: 'Enter your last name',
      key: 'last_name',
      component: (
        <Input
          placeholder='Enter your last name'
          value={formData.last_name}
          onChange={(e) => updateFormData('last_name', e.target.value)}
        />
      ),
    },
    {
      title: 'Enter your date of birth',
      key: 'birth_date',
      component: (
        <Input
          placeholder='YYYY-MM-DD'
          type='date'
          value={formData.birth_date}
          onChange={(e) => updateFormData('birth_date', e.target.value)}
        />
      ),
    },
    {
      title: 'Select your gender',
      key: 'gender',
      component: (
        <RadioGroup
          value={formData.gender}
          onChange={(value) => updateFormData('gender', value)}
        >
          <Stack direction='row'>
            <Radio value='male'>Male</Radio>
            <Radio value='female'>Female</Radio>
            <Radio value='nonbinary'>Non-Binary</Radio>
          </Stack>
        </RadioGroup>
      ),
    },
    {
      title: 'Enter your location',
      key: 'location',
      component: (
        <Input
          placeholder='Enter your location'
          value={formData.location}
          onChange={(e) => updateFormData('location', e.target.value)}
        />
      ),
    },
    {
      title: 'Write a short bio',
      key: 'bio',
      component: (
        <Input
          placeholder='Write something about you'
          value={formData.bio}
          onChange={(e) => updateFormData('bio', e.target.value)}
        />
      ),
    },
    {
      title: 'Upload your profile picture',
      key: 'image_url',
      component: (
        <Input
          type='file'
          accept='image/*'
          onChange={(e) => updateFormData('image_url', e.target.files?.[0] || null)}
        />
      ),
    },
  ];

  const handleNext = async () => {
    if (step === steps.length - 1) {
      try {
        const formDataToSend = new FormData();
        Object.entries(formData).forEach(([key, value]) => {
          formDataToSend.append(key, value);
        });
        await axios.post('/api/v1/register/', formDataToSend, {
          headers: { 'Content-Type': 'multipart/form-data' },
        });
        toast({ title: 'Registration Successful', status: 'success' });
        router.push('/');
      } catch (error) {
        toast({ title: 'Registration Failed', status: 'error' });
      }
      return;
    }
    setStep((prev) => prev + 1);
  };

  return (
    <div>
      <h2>{steps[step].title}</h2>
      <p>{steps[step].description}</p>
      {steps[step].component}
      <Button onClick={handleNext}>{step === steps.length - 1 ? 'Submit' : 'Next'}</Button>
    </div>
  );
};

export default MainRegister;
